import React from "react";

const Header = () => {
  const headerStyle = {
    padding: "20px 0",
    lineHeight: "1.5em"
  };

  const logoStyle = {
    fontSize: "6rem",
    fontWeight: "600",
    marginBottom: "2rem",
    lineHeight: "1em",
    color: "#ececec",
    textTransform: "lowercase",
    textAlign: "center"
  };
  return (
    <header style={headerStyle}>
      <h1 style={logoStyle}>
        <span role="img" aria-label="todo-logo">
          toDðŸŒ•S
        </span>
      </h1>
    </header>
  );
};

/**
 * const Header = () => {} is similar to
 * function Header()
 */

export default Header;
